import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'

export default class Showcase extends Component {
  static defaultProps = {
    defaultDescription: 'asdfasdf default description',
    data: [],
  }

  constructor (props) {
    super(props)
    this.state = {
      activeEl: 0,
    }

    this.onLeft = this.onLeft.bind(this)
    this.onRight = this.onRight.bind(this)
  }

  onLeft () {
    this.setState({ activeEl: Math.max(0, this.state.activeEl - 1) })
  }

  onRight () {
    this.setState({ activeEl: Math.min(this.props.data.length - 1, this.state.activeEl + 1) })
  }

  render () {
    const { activeEl } = this.state
    const data = this.props.data

    const item = this.props.data[activeEl]

    return (
      <>
        {/* left */}
        <div className='flex items-center w-full md:w-1/2'>
          <FontAwesomeIcon icon={faChevronLeft} className={(activeEl === 0 ? 'text-gray-400' : 'text-teal-500 hover:text-teal-600') + ' text-3xl m-4 lg:m-16 flex-shrink transition ease-out duration-500'} onClick={this.onLeft} />
          <div className='relative flex-grow' style={{ paddingBottom: '100%' }}>
            <img className='absolute object-scale-down w-full h-full' src={item.img} />
          </div>
          <FontAwesomeIcon icon={faChevronRight} className={(activeEl === data.length - 1 ? 'text-gray-400' : 'text-teal-500 hover:text-teal-600') + ' text-3xl m-16 flex-shrink transition ease-out duration-500'} onClick={this.onRight} />
        </div>
        {/* right */}
        <div className='w-full p-12 md:w-1/2'>
          <h2 className='mb-4 text-3xl'>{item.title}</h2>
          <ul className='mb-4 overflow-y-scroll font-light whitespace-pre-wrap' style={{ height: '20rem' }}>
            {item.info.map((li, i) => (
              <li key={`info_${i}`}>{li}</li>
            ))}
            <br />
            {item.origin}
            <br />
            {item.certs}
          </ul>
          {/* <hr className='my-6' /> */}
          {/* icons to scroll through origin/certs/productinfo/eventfeed */}
        </div>
      </>
    )
  }
}
